*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    font-family: 'montseratt', sans-serif;
}
.app{
    background-image: url(./assets/cold-bg.jpg);
    background-size: cover;
    background-position: bottom;
    transition: 0.4s ease-in-out;
    width: 500px;
    height: 700px;
    margin: 0 auto;
}
main{
    min-height: 100vh;
    padding: 0 25px;
}
.app.warm{
    background-image: url(./assets/warm-bg.jpg);
    background-size: cover;
    background-position: bottom;
    transition: 0.4s ease-in-out;
}
.search-bar{
    width: 100%;
    margin: 0 0 75px;
    padding: 15px;
}

.search-box .search-bar{
    appearance: none;
    background: none;
    border: none;
    outline: none;

    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 0px 0px 16px 16px;
    margin-top: -25px;
    color: #313131;
    font-size: 20px;
    transition: 0.4s ease;
}

.search-box .search-bar:focus{
    background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location{
    color: #fff;
    font-size: 32px;
    font-weight: 500;
    text-align: center;
    text-shadow: 3px 3px rgba(50, 50, 70, 0.5) ;
}
.location-box .date{
    text-align: center;
    color: grey;
    margin-top: 5px;
    font-size: 16px;
    font-weight: 700;
}
.weather-box{
    text-align: center;
}
.weather-box .temp{
    padding: 15px 25px;
    margin: 30px auto;
    position: relative;
    display: inline-block;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  font-size: 102px;
  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
  font-weight: 900;
  color: black;
}
.weather-box .weather{
    font-size: 32px;
    font-weight: 600;
    text-shadow: 2px 3px rgba(0, 0, 0, 0.2);
}
